<template>
  <Navbar 
  @searchValue="search = $event" 
  :lang="lang"
  @changeLang="changeLang"
  />
  <Notes 
  :notes="filterNotes"
  @delNote="deleteNote"
  @changeNote="changeNote"
   />
  <Modal
    @addNote="addNote"
    :currentId="currentId"
    v-show="modalOpen"
    @closeModal="closeModal"
    :edit="edit"
    :editNotes="editNotes"
    @editedNote=" editedNote"
  />
  <AddButton @openModal="openModal" />
</template>

<script>
import langs from "@/lang";
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
import AddButton from "@/components/AddButton.vue";
export default {
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton,
  },
  data() {
    return {
      currentId: 1,
      modalOpen: false,
      notes: [],
      edit: false,
      editNotes: {}, 
      search: '',
      lang: 'ru',
      langWords: {}
    };
  },
  created(){
    this.getNotes()
    localStorage.lang = localStorage.lang || 'ru'
    this.lang = localStorage.lang || 'ru'
    this.langWords = langs
    
  }, 
  computed:{
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLocaleLowerCase())) : this.notes
    }
  },
  methods: {
    openModal() {
      this.modalOpen = true;
    },
    closeModal(status) {
      this.modalOpen = status;
      setTimeout(() => this.edit = false, 500)
    },
    addNote(item) {
      this.notes.push(item);
      this.modalOpen = false
    },
    deleteNote(id){
     let index = this.notes.findIndex(note => note.id == id)
     this.notes.splice(index, 1)
    },
    getNotes(){
       const localNotes = localStorage.notes
       if(localNotes) {
         this.notes = JSON.parse(localNotes)
       }
    },
    changeNote(id){
      this.edit = this.modalOpen = true
      let pickedNotes = this.notes.find(note => note.id == id )
      this.editNotes = pickedNotes
    },
    editedNote(noteEdited){
      this.notes.forEach(note =>{
        if (note.id == noteEdited.id){
          note.title = noteEdited.title
          note.descr = noteEdited.descr
          note.date = noteEdited.date
        }
      })
    },
    changeLang(val){
      this.lang = localStorage.lang = val
    }
  },
  watch: {
    notes:{
      handler(newNotes){
        localStorage.notes =JSON.stringify(this.notes)
      },
      deep: true
    }
  },
};
</script>

