<template>
  <header class="header">
    <Transition name="header__notes">
    <div class="header__notes" v-show="header === true">
        <button @click="changeLang" class="header__lang" v-if="lang == 'ru'">UZ</button>
        <button @click="changeLang" class="header__lang" v-else>RU</button>
        <h1 class="header__title">Заметки</h1>
        <button class="header__search" @click="header =false">
            <img src="@/assets/img/search.svg" alt="">
        </button>
    </div>
       </Transition>
    <div class="header__forms" v-show="header === false">
        <button class="header__back" @click="header = true, search = '' ">
            <img src="@/assets/img/arrow.svg" alt="">
        </button>
        <input 
        type="text" 
        class="header__input" 
        placeholder="ПОИСК..." 
        v-model="search"
        >
        <button class="header__close" @click="search = '' ">
            <img src="@/assets/img/clear.svg" alt="">
        </button>
    </div>
  </header>
</template>

<script>
export default {
    props: ["lang"],
    data(){
        return{
            header: true,
            search: ''
        }
    },
    watch:{
        search(val){
            this.$emit('searchValue', val)
        }
    },
    methods:{
        changeLang(){
            this.$emit('changeLang', this.lang == 'ru' ? 'uz' : 'ru')
        }
    }

}
</script>

<style scoped>
.header{
  background: #F3EDF7; 
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 1px 3px rgba(0, 0, 0, 0.3);
  padding: 18px;
  height: 70px;

}
.header__notes,
.header__forms{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header__lang{
    font-size: 22px;
    font-weight: 700;
}
.header__title{
    font-size: 22px;
    line-height: 28px;
    color: #1C1B1F;
}
.header__input{
    display: block;
    width: 90%;
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    line-height: 20px;
    color: #9D9D9D;
}

.header__notes-enter-active,
.header__notesleave-active {
  transition: opacity 0.5s ease;
}

.header__notes-enter-from,
.header__notes-leave-to {
  opacity: 0;
  
}

</style>