<template>
  <div class="notes">
    <div class="container">
        <div class="notes__top">
            <h2 class="notes__title">
                {{notes.length > 0 ? 'Все заметки' : 'Нет заметок'}}
            </h2>
            <button class="notes__btn" @click="grid = !grid">
                <img src="@/assets/img/list.svg" alt="" v-if="grid" />
                <img src="@/assets/img/setka.svg" alt="" v-else/>
                <span class= "notes__btn-span">{{ grid ? 'Список' : 'Сетка' }}</span>
            </button>
        </div>
        <div class="notes__list" :class="{active: !grid}">
            <NoteItem :grid="grid"
            v-for="item in notes" 
            :key="item.id"
            :iteam="item"
            @delNote="$emit('delNote', item.id)"
            @changeNote="$emit('changeNote', item.id)"
            />
        </div>
    </div>
  </div>
</template>

<script>
import NoteItem from "@/components/NoteItem.vue";
export default {
    props:{
        notes:{
            typeof: Array,
            require: true
        }
    },
    components:{
        NoteItem
    },
    data(){
        return{
            grid: true
        }
    }
}
</script>

<style>
.notes{
    margin-top: 30px;
}
.notes__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.notes__title{
    font-size: 22px;
    line-height: 28px;
    color: #323232;
}
.notes__btn{
    width: 120px;
    height: 55px;
    background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #FFFBFE;
    box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15), 0px 1px 3px rgba(0, 0, 0, 0.3);
    border-radius: 16px;
    display: flex;
    gap: 12px;
    justify-content: center;
    align-items: center;
}
.notes__btn-span{
    font-family: "RM";
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1px;
    color: #6750A4;
}
.notes__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
    margin-top: 30px;
}
.notes__list.active{
    grid-template-columns: 1fr;
}
